.home-content {
    display: flex;
    height: 70vh;
    align-items: center;
    justify-content: center;
    color: white;
}

.logo {
    margin-left: 0.5rem;
    height: 2rem;
    width: 2rem;
}

.primaryBtn {
    background-color: white;
    border: none;
    color: #3a263e;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 12px;
    font-weight: bold;
    margin: 20px;
    margin-top: 25px;
}

.primaryBtn:hover {
    color: #724a7a;
}

.name-heading {
    font-size: 40px;
    font-weight: bold;
    padding: 15px;
}
  
  #span {
    color: #3a263e;
}
  
@media only screen and (max-width: 480px) {

    .home-content {
      height: 80vh;
    }
}
.about-heading {
    color: #3a263e;
    font-weight: bold;
}

.about-subheading {
    color: white;
    font-style: italic;
}

#about {
    padding: 30px;
}

.description {
    text-align: justify;
    text-justify: inter-word;  
}

.profile {
    border-radius: 25px;
    margin: 15px;
    height: 35vh;
    margin-left: 2rem;
}

@media only screen and (max-width: 480px) {
    .about-content {
        display: flex;
        height: 70vh;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }
}

@media only screen and (min-width: 1000px) {
    #about {
        margin-top: 2.5rem;
        width: 800px;
    }

    .profile {
        width: 45vh;
        height: 45vh;
    }
}
.CircularProgressbar {
    height: 30vh;
    width: 20vh;
    padding: 0.5rem;
    margin: -1rem;
}

.skills-subheading {
    color: white;
    text-align: center;
    font-size: 10pt;
    font-weight: bold;
}

.skills-header {
    font-weight: bold;
    color: #3a263e;
    margin-bottom: 1.5rem;
}

#skills {
    width: 120vh;
}

.hr {
    color: white;
}

@media only screen and (max-width: 480px) {
    .CircularProgressbar {
        height: 24vh;
        width: 24vh;
    }
}
.project-card {
    margin-bottom: 10px; 
    box-shadow: 1px 3px 3px 1px #b582b0;
    transition: 0.5s all ease-in-out;
}

.project-card:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    z-index: 1;
}

.projectBtn {
    margin: 5px;
    margin-left: 1.5rem;
    align-content: center;
    background-color: #8b5297 !important;
    border: none !important;
}

.projects-header {
    font-weight: bold;
    color: #3a263e;
    margin-bottom: 0.5rem;
}

#projects {
    width: 165vh;
}

.proj-content {
    padding-top: 1rem;
 
}

.card-title {
    font-weight: bold !important;
    font-size: 12pt !important;
    color: #3a263e;
    text-align: center;
}

@media only screen and (max-width: 480px) {
    .project-card {
        width: 50vh;
    }
}
.contact-heading {
    color: white;
    font-weight: bold;
}

.contactBtn {
    margin: 5px;
    margin-left: 1.5rem;
    align-content: center;
    background-color: #8b5297 !important;
    border: none !important;
    width: 47vh;
}

.submitBtn {
    background-color: #8b5297;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 8px;
}

.contact-form {
    margin-top: 4rem;
    margin-left: 2rem;
}

#contact-content {
    width: 50vh;
}

.form-control {
    margin: 1rem;
}

@media only screen and (max-width: 480px) {
    .contact-content {
        display: flex;
        height: 70vh;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }

    .contact-form {
        width: 40vh;
        margin-left: 13px;
    }

    .contactBtn {
        width: 40vh;
        margin-left: 40px;
    }
}

@media only screen and (min-width: 1000px) {
    #contact {
        margin-top: 2.5rem;
        width: 800px;
    }
}
.navbar-toggler:focus,
.navbar-toggler-icon:focus {
    box-shadow: none;
}

.nav-link {
    color: white;
    font-size: 15px;
    margin-left: 0.5rem;
    margin: 1.5rem;
    font-weight: bold;
  }
  
.nav-link:hover {
    color: #3a263e;
}
  
.navbar-nav {
    margin-right: 2rem;
    margin-top: 0.5rem;
}  
  
@media only screen and (max-width: 480px) {
    .nav-link {
      margin-left: 0;
      margin: 0;
      font-size: 10pt;
    }
  
    .navbar-nav {
      position: absolute;
      margin: 0.5rem;
      padding-left: 0.5rem;
    }
}
body {
  background-color: #ad9cb6;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}



